* {
  font-family: Inter, sans-serif;
}

body {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

.header {
  background-color: #F55A5A;
  padding: 10px;
  display: flex;
  align-items: center;
  color: white;
  justify-content: center;
}

.header>.earth-icon {
  padding: 7px;
}

/*content*/
section {
  width: 60%;
  margin: auto auto;
  /* padding: 30px; */
  /* border: 2px solid lime; */
}

/*each card containing photo, title, description*/
.card {
  display: flex;
  width: 50%;
  justify-content: space-between;
  align-items: center;
  margin: auto;
  /* border: 1px solid red; */
  padding: 30px;
}

/*image of the place*/
.place-img-container {
  width: 100%;
  /* border: 1px solid blue; */
  margin: 10px;

}

.place-img {
  width: 100%;
  /* border: 1px solid green; */
}

.image-credit {
  position: absolute;
  left: 50px;
  bottom: 5px;
  color: #918E9B;
}

.location-container {
  display: flex;
  align-items: center;
  flex-wrap: nowrap;
  justify-content: flex-end;
}

.fa-location-dot {
  padding-right: 5px;
  color: #F55A5A;
}

.location {
  text-transform: uppercase;
  font-size: 0.95em;
  padding-right: 10px;
}

.gmaps-url {
  font-size: 12px;
  font-weight: 400;
  color: #918E9B;
  text-decoration: underline;
  text-transform: none;
}

.link {
  cursor: pointer;
}

/*footer*/
.footer {
  position: relative;
  bottom: 0;
  margin-top: 30px;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.social-media-links {
  margin: 0;
  list-style-type: none;
  display: flex;
  justify-content: space-around;
  min-width: 50%;
  max-width: 50%;
}